
.vux-popover{position:absolute;left:0;top:0;background-color:#35495e;color:#fff;border-radius:3px;z-index:500
}
.vux-popover-arrow{position:absolute;width:0;height:0
}
.vux-popover-arrow-up{border-left:5px solid transparent;border-right:5px solid transparent;border-bottom:5px solid #35495e;left:50%;-ms-transform:translateX(-50%);transform:translateX(-50%);top:-5px
}
.vux-popover-arrow-down{border-left:5px solid transparent;border-right:5px solid transparent;border-top:5px solid #35495e;left:50%;-ms-transform:translateX(-50%);transform:translateX(-50%);bottom:-5px
}
.vux-popover-arrow-left{border-top:5px solid transparent;border-bottom:5px solid transparent;border-right:5px solid #35495e;top:50%;-ms-transform:translateY(-50%);transform:translateY(-50%);left:-5px
}
.vux-popover-arrow-right{border-top:5px solid transparent;border-bottom:5px solid transparent;border-left:5px solid #35495e;top:50%;-ms-transform:translateY(-50%);transform:translateY(-50%);right:-5px
}


